.newsContainer {
  background: #f6f3f3;
  grid-area: cont;
  height: fit-content;
  border-radius: 10px;
  padding: 20px;
  display: grid;
  justify-items: center;

  .searchBlock {
    width: 600px;
    background: #e2dfde;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-radius: 10px;

    button {
      width: fit-content;
    }
  }

  .newsBlock {
    padding-top: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}