.navBarContainer {
  grid-area: nav;
  align-items: center;
  display: grid;
  width: fit-content;
  height: 200px;
  text-align: center;
  padding: 20px;
  background: #f8f5f5;
  border-radius: 10px;
  overflow: auto;

  a {
    text-decoration: none;
    color: #000;
    line-height: 2;
  }

}



